export const updateBook = async (form,page,limit,{
    author,
    genre,
    key
}) => {
    if(!form.photo || form.photo == "") {
        try {
            const data =  await apolloClient.mutate({
               mutation : UPDATE_BOOK,
               variables : {
                   name : form.name,
                   bookId : form.bookId,
                   genre : form.genre,
                   description : form.description,
                   author_id : form.author_id,
                   imgDefault : form.imgDefault,
               },
               update: async (cache, { data : { updateBook } }) => {
                    const data = cache.readQuery({
                        query : QUERY_PAGE_BOOKS,
                        variables : {
                            page,
                            limit,
                            author,
                            key,
                            genre,
                        }
                    });
                    const index = data.booksPage.books.findIndex(book => book._id == form.bookId);
                    data.booksPage.books[index] = {...updateBook, _id : form.bookId };
                    console.log(`${updateBook.name} was updated`);
                    cache.writeQuery({
                        query : QUERY_PAGE_BOOKS,
                        variables : {
                            limit,
                            page,
                            author,
                            genre,
                            key,
                        },
                        data : data
                    });
                    return data;
               }
            });
            return data;
        } catch(err) {
            console.log(err);
        }
    } else {
        try {
            const data =  await apolloClient.mutate({
                mutation : UPDATE_BOOK,
                variables : {
                   name : form.name,
                   bookId : form.bookId,
                   genre : form.genre,
                   author_id : form.author_id,
                   photo : form.photo,
                   description : form.description,
                },
                update: async (cache, { data : { updateBook } }) => {
                    const data = cache.readQuery({
                        query : QUERY_PAGE_BOOKS,
                        variables : {
                            page,
                            limit,
                            author,
                            genre,
                            key
                        }
                    });
                    const index = data.booksPage.books.findIndex(book => book._id == form.bookId);
                    data.booksPage.books[index] = {_id : form.bookId, ...updateBook };
                    console.log(`${updateBook.name} was updated`);
                    cache.writeQuery({
                        query : QUERY_PAGE_BOOKS,
                        variables : {
                            limit,
                            page,
                            author,
                            genre,
                            key,
                        },
                        data : data,
                    });
                    return data;
                }
            });
            return data;
        } catch(err) {
            console.log(err);
        }
    }
}

export const deletedBook = async (bookId,page,limit) => {
    try {
        const { data } = await apolloClient.mutate({
            mutation : DELETE_BOOK,
            variables : {
                bookId,
            },
            update : async (cache) => {
                const data = cache.readQuery({
                    query : QUERY_PAGE_BOOKS,
                    variables : {
                        limit,
                        page,
                    }
                });
                const index = data.booksPage.books.findIndex( book => book._id == bookId );
                if( index == -1 ) return false;
                data.booksPage.books.splice(index,1);
                cache.writeQuery({
                    query  : QUERY_PAGE_BOOKS,
                    variables : {
                        limit,
                        page,
                    },
                    data,
                });
                for( let i = 0; i < 999; i++) {
                    const { data : currentPage } = await apolloClient.query({
                        query : QUERY_PAGE_BOOKS,
                        variables : {
                            limit,
                            page : page + i
                        }
                    });

                    const { data : {
                        booksPage : {
                            books : nextBooks,
                            pageInfo : nextPageInfo
                        }
                    }} = await apolloClient.query({
                        query : QUERY_PAGE_BOOKS,
                        variables : {
                            limit,
                            page : page + i  + 1,
                        }
                    });
                    if(nextBooks.length == 0) break;
                    const [firstBook,...anotherBooks ] = nextBooks;
                    currentPage.booksPage.books.push(firstBook);

                    cache.writeQuery({
                        query : QUERY_PAGE_BOOKS,
                        variables : {
                            limit,
                            page : page + i
                        },
                        data : currentPage
                    });
                    
                   await apolloClient.query({
                        query : QUERY_PAGE_BOOKS,
                        variables : {
                            limit,
                            page : page + i,
                        }
                    });
                    
 
                    cache.writeQuery({
                        query : QUERY_PAGE_BOOKS,
                        variables : {
                            limit,
                            page : page + i + 1,
                        },
                        data : {
                            booksPage : {
                                books : [...anotherBooks],
                                pageInfo : nextPageInfo,
                                __typename: "BooksPaginate"
                            }
                        }
                    });

                    const result = cache.readQuery({
                        query : QUERY_PAGE_BOOKS,
                        variables : {
                            limit,
                            page : page + i
                        }
                    });
                    
                    console.log(result);
                }
                return data;
            }
        });
        return data;
    }catch(err) {
        console.log(err);
    }
}


