export const updateAuthor = async ({
        author_id,
        name,
        age
    },limit,page
) => {
    try {
        const data = await apolloClient.mutate({
            mutation : UPDATE_AUTHOR,
            variables : {
                author_id,
                name,
                age : parseInt(age)
            },
            update : (cache,{ data : { updateAuthor }}) => {
                if(!updateAuthor) return new Error('Something went wrong please try again');
                cache.readQuery({
                    query : QUERY_PAGE_AUTHOR,
                    variables : {
                        limit,
                        page
                    },
                });
            }
        });
        return data;
    } catch(error) {
        console.log(error)
        const msg = error.toString().split(":")[2];
        return  { error : msg }
    }
}

export const deleteAuthor = async (author_id,limit,page) => {
    try {
        const data = await apolloClient.mutate({
            mutation  : DELETE_AUTHOR,
            variables : {
                author_id,
            },
            update : async (cache, { data : deleteAuthor }) => {
                try {
                    const { error } = deleteAuthor;
                    if( error ) new Error("Something wen't wrong ");
                    const data = cache.readQuery({
                        query : QUERY_PAGE_AUTHOR,
                        variables : {
                            limit,
                            page,
                        },
                    });
                    const delIndex = data.authorsPage.authors.findIndex( e => e._id == author_id );
                    if( delIndex !== -1 ) data.authorsPage.authors.splice(delIndex,1);
                    cache.writeQuery({
                        query : QUERY_PAGE_AUTHOR,
                        variables : {
                            limit,
                            page
                        },
                        data,
                    });
                    for(let i = 0; i < 999; i++ ) {
                        // Query current page
                        const { data : {
                            authorsPage : {
                                authors :  currentAuthors,
                                pageInfo : currentPage
                            }
                        }}= await apolloClient.query({
                            query : QUERY_PAGE_AUTHOR,
                            variables : {
                                limit,
                                page : page + i,
                            }
                        });
                        const {
                            data : {
                                authorsPage : {
                                    authors : nextAuthors,
                                    pageInfo : nextPage,
                                }
                            }
                        } = await apolloClient.query({
                            query : QUERY_PAGE_AUTHOR,
                            variables : {
                                limit,
                                page : page  + i + 1.
                            }
                        });
                        if(nextAuthors.length <= 0 ) break;
                        const [firstAuthor,...anotherAuthors] = nextAuthors;
                        currentAuthors.push(firstAuthor);
                        cache.writeQuery({
                            query : QUERY_PAGE_AUTHOR,
                            variables : {
                                limit,
                                page : page + i
                            },
                            data : {
                                authorsPage : {
                                    authors : currentAuthors,
                                    pageInfo : currentPage,
                                    __typename : "AuthorsPaginate"
                                },
                            }
                        });
                        cache.writeQuery({
                            query : QUERY_PAGE_AUTHOR,
                            variables :  {
                                limit,
                            page : page + i + 1
                            },
                            data  : {
                                authorsPage : {
                                    authors : [...anotherAuthors],
                                    pageInfo : nextPage,
                                    __typename : 'AuthorsPaginate'
                                }
                            }
                        });
                    }   
                } catch(err) {
                    console.log(err);
                }
                return data;
            }
        });
        return data
    } catch(err) {
        const msg = err.toString().split(":")[2];
        return {
            error : msg,
        }
    }
}

