export const updateAdmin = async({
    name,
    email,
    pwd,
    avatar,
    imgDefault,
}) => {
        if(!avatar || avatar == "" ) {
            try {
                const data = await apolloClient.mutate({
                    mutation : UPDATE_ADMIN,
                    variables : {
                        username : name,
                        email : email,
                        password : pwd,
                        imgDefault : imgDefault,
                    },
                    update : async ( cache, { data : { updateAdmin }} ) => {
                        store.commit('SET_USER',updateAdmin);
                    }
                });
                return data;
        } catch(err) {
                console.log(err);
            }
        } else  {
            try {
                const data = await apolloClient.mutate({
                    mutation : UPDATE_ADMIN,
                    variables : {
                        username : name,
                        email : email,
                        password : pwd,
                        imgDefault : imgDefault,
                        avatar : avatar,
                    },
                    update : async (cache,{ data : { updateAdmin }}) => {
                        store.commit('SET_USER',updateAdmin);
                    }
                });
                return data;
            } catch(err) {
                console.log(err)
            }
        }
}



export const addGenre = async(name) => {
    try {
        const data = await apolloClient.mutate({
            mutation : ADD_GENRE,
            variables : {
                name,
            },
            update : (cache,{ data : addGenre }) => {
                const update = addGenre;
                const data = cache.readQuery({
                    query : QUERY_GENRES
                });
                data.genres.unshift(update.addGenre);
                cache.readQuery({
                    query : QUERY_GENRES,
                    data,
                });
                return data;
            }
        });
        return data;
    } catch(err) {
        console.log(err);
    }
}


